@import url('https://fonts.googleapis.com/css?family=Encode+Sans+Expanded:400,500,600,700,800,900');

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.login-check{
  height: 100vh;
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}
.admin-button{
  margin-right: 10px;
}
.user-button{
  margin-left: 10px;
}
.main-page,.add-page{
  position: absolute;
  height: 100vh;
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: justify;
      justify-content: space-between;
}
.logo{
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.loading-products-info,.no-products-info{
  background: #00BCD4;
  position: relative;
  padding: 5px 0;
  bottom: 0px;
}
.loading-products-info h1,.no-products-info h1{
  text-align: center;
  font-family: 'Encode Sans Expanded', sans-serif;
  text-transform: uppercase;
  color: rgba(255,255,255,.8);
  font-size: 20px;
}
.catalog-info{
  background: #00BCD4;
  position: relative;
  padding: 5px 0;
  bottom: 0px;
}
.catalog-info h3{
  text-align: center;
  font-family: 'Encode Sans Expanded', sans-serif;
  text-transform: uppercase;
  color: rgba(255,255,255,.8);
  font-size: 14px;
}
.catalog-info h3 span{
  margin: 0 7px;
  font-size: 20px;
  color: white;
  border-bottom: 1px solid rgba(0,0,0,.5);
}
.catalog-info .remove-all-button{
  position: absolute;
  margin: 0 auto;
  top: -15px;
  left: 70px;
}
.catalog{
  list-style: none;
  width: 100%;
  text-align: center;
  overflow-y: auto;
  padding: 0;
  margin: 0;
}
.item-block{
  overflow-x: auto;
  height: 400px;
  display: inline-table;
  width: 180px;
  padding: 10px;
  -webkit-box-shadow: 0 0 15px rgba(0,0,0,.3);
          box-shadow: 0 0 15px rgba(0,0,0,.3);
  margin: 20px;
  position: relative;
}
.item-remove-button{
  position: absolute;
  top: -10px;
  right: -10px;
}
.item-name{
  text-transform: uppercase;
  font-size: 15px;
  color: #6e797a;
}
.item-image{
  height: 150px;
  width: 100%;
  -webkit-box-shadow: 0 0 10px rgba(0,0,0,.5);
          box-shadow: 0 0 10px rgba(0,0,0,.5);
  opacity: .8;
  -webkit-transition: opacity .3s;
  -o-transition: opacity .3s;
  transition: opacity .3s;
}
.item-image:hover{
  opacity: 1;
}
.item-price{
  text-align: left;
  font-size: 22px;
  margin-left: 10px;
  font-weight: bold;
  color: #333738;
}
.item-price span{
  font-size: 18px;
}
.item-description{
  color: #6e797a;
  font-size: 18px;
  text-align: left;
}
.item-remove-button{
  cursor: pointer;
  color: white;
  -webkit-transition: color .3s;
  -o-transition: color .3s;
  transition: color .3s;
}
.item-remove-button:hover{
  color: red;
}
.add-field{
  position: relative;
  margin: 0 auto;
  min-height: 400px;
}
.not-allowed-to-add{
  text-align: center;
}
.add-field h2,.not-allowed-to-add h1{
  text-transform: uppercase;
  font-size: 15px;
  color: #6e797a;
  text-align: center;
}
.not-allowed-to-add h1{
  font-size: 24px;
}
.add-button{
  margin-top: 15px;
  width: 100%;
}
.add-success{
  position: absolute;
  font-size: 20px;
  color: #6e797a;
  left: -200px;
  top: 100px;
  opacity: 1;
  -webkit-transition: left .5s, opacity .5s;
  -o-transition: left .5s, opacity .5s;
  transition: left .5s, opacity .5s;
}

/* ANIMATION LOADER */

.move-to-right{
  left: 10px;
}
.reduce-opacity{
  opacity: 0;
}

.fade-enter {
  opacity: 0;
  -webkit-transform: scale(0);
      -ms-transform: scale(0);
          transform: scale(0);
}

.fade-enter-active {
  opacity: 1;
  -webkit-transform: scale(1);
      -ms-transform: scale(1);
          transform: scale(1);
  -webkit-transition: opacity .3s ease-out, -webkit-transform .3s ease-out;
  transition: opacity .3s ease-out, -webkit-transform .3s ease-out;
  -o-transition: opacity .3s ease-out, transform .3s ease-out;
  transition: opacity .3s ease-out, transform .3s ease-out;
  transition: opacity .3s ease-out, transform .3s ease-out, -webkit-transform .3s ease-out;
}

.fade-leave {
  opacity: 1;
  -webkit-transform: scale(1);
      -ms-transform: scale(1);
          transform: scale(1);
}

.fade-leave-active{
  opacity: 0;
  -webkit-transform: scale(0);
      -ms-transform: scale(0);
          transform: scale(0);
  -webkit-transition: opacity .3s ease-out, -webkit-transform .3s ease-out;
  transition: opacity .3s ease-out, -webkit-transform .3s ease-out;
  -o-transition: opacity .3s ease-out, transform .3s ease-out;
  transition: opacity .3s ease-out, transform .3s ease-out;
  transition: opacity .3s ease-out, transform .3s ease-out, -webkit-transform .3s ease-out;
}

.fade-appear {
  opacity: 0;
}

.fade-appear-active{
  opacity: 1;
  -webkit-transition: opacity 1s ease-out;
  -o-transition: opacity 1s ease-out;
  transition: opacity 1s ease-out;
}

.loader{
  margin: 0 auto;
}
.loader-item{
  display: inline-block;
  border-radius: 50%;
  margin: 10px;
  height: 15px;
  width: 15px;
  background: #00BCD4;
}

.loader-item:nth-child(1){
  -webkit-animation: 1s loaderAnim infinite;
          animation: 1s loaderAnim infinite;
}

.loader-item:nth-child(2){
  -webkit-animation: 1s loaderAnim infinite .1s;
          animation: 1s loaderAnim infinite .1s;
}

.loader-item:nth-child(3){
  -webkit-animation: 1s loaderAnim infinite .2s;
          animation: 1s loaderAnim infinite .2s;
}

@-webkit-keyframes loaderAnim {
  0% {-webkit-transform: scale(1);transform: scale(1);}
  50% {-webkit-transform: scale(0.5);transform: scale(0.5);}
  100% {-webkit-transform: scale(1);transform: scale(1);}
}

@keyframes loaderAnim {
  0% {-webkit-transform: scale(1);transform: scale(1);}
  50% {-webkit-transform: scale(0.5);transform: scale(0.5);}
  100% {-webkit-transform: scale(1);transform: scale(1);}
}



